.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

[data-tabs=parent] {
  max-width: 100%;
  overflow: hidden;
}
[data-tabs=parent].no-transition > [data-tabs=control],
[data-tabs=parent].no-transition > [data-tabs=content],
[data-tabs=parent].no-transition > [data-tabs=element] {
  transition: none;
}
[data-tabs=parent].no-transition-global [data-tabs=control],
[data-tabs=parent].no-transition-global [data-tabs=content],
[data-tabs=parent].no-transition-global [data-tabs=element] {
  transition: none;
}

[data-tabs=parent] [data-tabs=content] {
  position: relative;
}

[data-tabs=element] {
  position: absolute;
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.3s ease-in, visibility 0.3s ease-in;
}

[data-tabs=element].for-load {
  position: static;
  visibility: visible;
  opacity: 1;
  transition: none;
}

[data-tabs=element].is-active {
  z-index: 1;
  visibility: visible;
  opacity: 1;
}

[data-accordion=element].is-active > [data-accordion=content] {
  max-height: 100%;
}

[data-accordion=parent].no-transition [data-accordion=content] {
  transition: none;
}

[data-accordion=content] {
  max-height: 0;
  transition: max-height 0.3s;
  overflow: hidden;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  transform: scale(1.1);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  transform: translateZ(0);
  background-color: rgba(0, 0, 0, 0.6);
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  min-width: 360px;
  min-height: 100%;
  padding: 0 15px;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  box-sizing: border-box;
  width: 334px;
  padding: 38px;
  background-color: #080808;
  border: 2px solid #ffffff;
  border-radius: 15px;
}
@media (min-width: 768px) {
  .modal__content {
    min-width: 698px;
  }
}
@media (min-width: 1920px) {
  .modal__content {
    min-width: 1248px;
    padding: 115px 298px 100px 125px;
  }
}
.modal__content::before {
  content: "";
  position: absolute;
  width: 177px;
  height: 19px;
  top: 0px;
  left: calc(50% - 88px);
  background: url("../img/svg/modal-triangle.svg");
  background-size: contain;
}
@media (min-width: 768px) {
  .modal__content::before {
    width: 357px;
    height: 38px;
    left: calc(50% - 178px);
  }
}
@media (min-width: 768px) {
  .modal__content::after {
    content: "";
    position: absolute;
    width: 280px;
    height: 360px;
    bottom: -41px;
    right: 12px;
    background: url("../img/modal-background.webp") no-repeat;
    background-size: contain;
  }
}
@media (min-width: 768px) and (min-width: 1920px) {
  .modal__content::after {
    width: 356px;
    height: 416px;
    bottom: -10px;
    right: 0;
  }
}
.modal__form {
  position: relative;
  display: grid;
  grid-template-columns: 1fr;
}
@media (min-width: 768px) {
  .modal__form {
    max-width: 274px;
    grid-template-columns: 1fr;
  }
}
@media (min-width: 1920px) {
  .modal__form {
    grid-template-columns: repeat(2, 1fr);
    min-width: 775px;
    column-gap: 75px;
    row-gap: 65px;
  }
}
.modal__form::before {
  content: "";
  position: absolute;
  width: 98px;
  height: 97px;
  top: -10%;
  left: -10%;
  background-color: #2ba2b2;
  filter: blur(105px);
  border-radius: 50%;
}
@media (min-width: 768px) {
  .modal__form::before {
    width: 134px;
    height: 133px;
    top: -30%;
    left: -10%;
  }
}
.modal__form::after {
  content: "";
  position: absolute;
  width: 98px;
  height: 97px;
  bottom: -10%;
  right: -10%;
  background-color: #2ba2b2;
  filter: blur(105px);
  border-radius: 50%;
}
@media (min-width: 768px) {
  .modal__form::after {
    width: 134px;
    height: 133px;
    bottom: -30%;
    right: -150%;
  }
}
@media (min-width: 1920px) {
  .modal__form::after {
    right: -30%;
  }
}
.modal__close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
@media (min-width: 768px) {
  .modal__close-btn {
    top: 20px;
    right: 20px;
  }
}
@media (min-width: 1920px) {
  .modal__close-btn {
    top: 40px;
    right: 40px;
  }
}
.modal__close-btn svg {
  display: block;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: auto;
}
.modal__button {
  padding: 19px 50px;
}
@media (min-width: 1920px) {
  .modal__button {
    width: fit-content;
    padding: 25px 66px;
  }
}

