.custom-input label input {
  flex-grow: 1;
  padding: 10px 10px 10px 0;
  background-color: transparent;
  border: none;
  color: #ffffff;
  font-family: "Nunito", sans-serif;
  font-size: 18px;
  font-weight: 300;
  border-bottom: 2px solid #ffffff;
  transition: border 0.3s ease;
